/* –ó–∞–ø—Ä–µ—â–∞–µ–º –≤—ã–¥–µ–ª–µ–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ */
* {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

/* –ü–æ–ª–Ω–æ–µ –ø–æ–∫—Ä—ã—Ç–∏–µ —ç–∫—Ä–∞–Ω–∞, —É–±–∏—Ä–∞–µ–º —Å–∫—Ä–æ–ª–ª–∏–Ω–≥ */
html, body {
    height: 100vh;
    width: 100vw;
    margin: 0;
    font-family: "Arial", sans-serif;
    background: linear-gradient(135deg, #0b0c10, #1f2833);
    color: #66fcf1;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    overflow: hidden; /* üî• –ü–æ–ª–Ω–æ—Å—Ç—å—é —É–±–∏—Ä–∞–µ–º —Å–∫—Ä–æ–ª–ª–∏–Ω–≥ */
    touch-action: none; /* üî• –ë–ª–æ–∫–∏—Ä—É–µ–º –∂–µ—Å—Ç—ã —Å–∫—Ä–æ–ª–ª–∏–Ω–≥–∞ –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö */
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä Mini App */
.container {
    max-width: 400px;
    width: 90%;
    background: rgba(20, 20, 30, 0.95);
    padding: 30px;
    border-radius: 15px;
    box-shadow: 0px 0px 30px rgba(102, 252, 241, 0.3);
    animation: fadeIn 0.8s ease-in-out;

    /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    /* –§–∏–∫—Å–∏—Ä—É–µ–º –≤—ã—Å–æ—Ç—É –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
    min-height: 60vh;
    position: relative;
}

/* –ê–Ω–∏–º–∞—Ü–∏—è –ø–æ—è–≤–ª–µ–Ω–∏—è */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}




/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ */
.title {
    font-size: 28px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #66fcf1;
    text-shadow: 0 0 10px rgba(102, 252, 241, 0.8);
    margin-bottom: 10px;
}

/* –ü–æ–¥–∑–∞–≥–æ–ª–æ–≤–æ–∫ */
.subtitle {
    font-size: 16px;
    font-weight: lighter;
    color: rgba(102, 252, 241, 0.8);
    margin-bottom: 20px;
}

/* –ú–µ–Ω—é */
.menu {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

/* –ö–Ω–æ–ø–∫–∏ –º–µ–Ω—é */
.menu-button {
    background: linear-gradient(135deg, #3a0ca3, #7209b7);
    color: white;
    font-size: 16px;
    padding: 12px 20px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    box-shadow: 0px 0px 10px rgba(114, 9, 183, 0.5);
}

.menu-button:hover {
    transform: scale(1.05);
    box-shadow: 0px 0px 20px rgba(114, 9, 183, 0.8);
}

/* –ê–Ω–∏–º–∞—Ü–∏—è –ø–æ—è–≤–ª–µ–Ω–∏—è */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}
